.header {
  height: 100vh;
  text-align: center;

  &__main {
    @include page-grid;

    @include on-tablet {
      grid-template-rows: repeat(4, auto);
    }

    @include on-desktop {
      grid-template-rows: repeat(3, auto);
    }
  }

  &__subtitle {
    margin-block: 32px;
    grid-column: 1 / -1;

    @include on-tablet {
      margin-top: 80px;
      margin-bottom: 16px;
      grid-column: 1 / 5;
      text-align: left;
    }

    @include on-desktop {
      margin-top: 135px;
      margin-bottom: 8px;
      grid-column: 1 / 3;
      grid-row: 1 / 2;
    }

    @include on-large-desktop {
      margin-top: 144px;
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;
    grid-column: 1 / -1;

    @include on-tablet {
      align-self: center;
      grid-row: 2 / 4;
    }

    @include on-desktop {
      grid-row: 2 / 3;
    }
  }

  &__cookie {
    background-image: url(.././images/logos/header-cookie.png);
    height: 290px;
    width: auto;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    z-index: -1;
    grid-column: 1 / -1;

    @include on-tablet {
      height: 328px;
      grid-row: 2 / 4;
    }

    @include on-desktop {
      height: 546px;
      grid-row: 1 / -1;
      margin-bottom: 66px;
    }

    @include on-large-desktop {
      height: 586px;
      margin-bottom: 26px;
    }
  }

  &__button {
    display: flex;
    height: 56px;
    border-radius: 32px;
    border: 2px $accent-orange solid;
    background-color: $light-pink;
    color: $accent-orange;

    grid-column: 1 / -1;
    justify-content: center;
    align-items: center;
    gap: 4px;

    @extend %uppercase;

    &::before {
      content: '';
      background-image: url(../images/icons/icon-button-default.png);
      background-size: contain;
      background-repeat: no-repeat;
      display: block;
      width: 24px;
      height: 24px;
    }

    &:hover {
      background-color: $accent-orange;
      color: $white;

      &::before {
        background-image: url(../images/icons/icon-cookie-hover.png);
      }
    }

    &:active {
      border-color: $dark-orange;
      background-color: $dark-orange;
    }

    &:disabled {
      border-color: $light-orange;
      background-color: $light-pink;
      color: $light-orange;

      &::before {
        background-image: url(../images/icons/icon-cookie-disabled.png);
      }
    }

    @include on-tablet {
      grid-column: 3 / 7;
      margin-top: 80px;
      margin-bottom: 228px;
    }

    @include on-desktop {
      grid-column: 1 / 3;
      grid-row: 3 / 4;
      margin-top: 64px;
      margin-bottom: 161px;
    }

    @include on-large-desktop {
      margin-bottom: 138px;
    }
  }
}
