.page {
  scroll-behavior: smooth;
  font-family: Inter, sans-serif;
  background-color: $light-pink;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;

    opacity: 0;
    pointer-events: none;

    &:target {
      opacity: 1;
      pointer-events: all;
      overflow: hidden;
    }
  }
}
