@import 'utils';
@import 'fonts';
@import 'typography';
@import 'blocks/page';
@import 'blocks/header';
@import 'blocks/top-bar';
@import 'blocks/menu';
@import 'blocks/nav';

// #region icon
.icon {
  display: block;
  height: 24px;
  width: 24px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;

  &--menu {
    background-image: url(../images/icons/icon-menu.png);

    @include on-desktop {
      display: none;
    }
  }

  &--close {
    background-image: url(../images/icons/icon-close.png);
  }
}

// #endregion

.section-title {
  &--orange {
    color: $accent-orange;
  }

  &--white {
    color: $white;
  }
}

.main {
  display: none !important;

  @include content-padding-inline;
}

.blog {
  display: none !important;
}

.products__product {

  // @include on-tablet {
  //   @include page-grid;
  // }
}

.product {
  @include page-grid;

  &__img {
    width: 100%;
    height: 100%;
    grid-column: 1 / -1;

    // @include on-tablet {
    //   grid-column: 1 / 3
    // }
  }

  &__category {
    @extend %uppercase;

    color: $grey;
    grid-column: 1 / -1;
  }

  &__description {
    grid-column: 1 / -1;
  }
}
