.nav {
  &__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 24px;
    list-style: none;
    text-align: center;

    @include on-desktop {
      flex-direction: row;
      gap: 64px;
    }
  }

  &__link {
    text-decoration: none;
    color: $black;

    @extend %body-text;

    &--phone-number {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 4px;
      text-decoration: none;
      color: $accent-orange;

      @extend %uppercase;

      &::before {
        content: '';
        height: 24px;
        width: 24px;
        background-image: url(../images/icons/icon-phone.png);
        background-size: 24px;
      }
    }
  }
}
